import Vue from 'vue'
import VueRouter, { RouteConfig, Route } from 'vue-router'
import Login from '@/pages/Login.vue'

Vue.use(VueRouter)

const routes: Array<RouteConfig> = [
  {
    path: '/',
    name: 'Login',
    component: Login,
    meta: { layout: 'login-layout' }
  },
  {
    path: '/authors',
    name: 'authors',
    component: () => import('@/pages/authors/Authors.vue')
  },
  {
    path: '/authors/:id',
    name: 'author',
    component: () => import('@/pages/authorPage/Author.vue'),
    props: (route: Route) => {
      return {
        id: Number(route.params.id)
      }
    }
  },
  {
    path: '/books',
    name: 'books',
    component: () => import('@/pages/books/Books.vue')
  },
  {
    path: '/books/:id',
    name: 'book',
    component: () => import('@/pages/bookPage/Book.vue'),
    props: (route: Route) => {
      return {
        id: Number(route.params.id)
      }
    }
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
