






import Vue from 'vue'
import { Component } from 'vue-property-decorator'

import MainLayout from '@/layout/wrappers/MainLayout.vue'
import LoginLayout from '@/layout/wrappers/LoginLayout.vue'

@Component({
  components: {
    MainLayout,
    LoginLayout
  }
})
export default class AppPage extends Vue {
  get layout() {
    return this.$route.meta.layout ?? 'main-layout'
  }
}
