































































import Vue from 'vue'
import { Component } from 'vue-property-decorator'
import { sidebarModule } from '@/store/modules/sidebar.ts'

@Component
export default class AppSidebar extends Vue {
  get sidebarState() {
    return sidebarModule.open
  }

  get drawer() {
    return sidebarModule.drawer
  }

  set drawer(val: string) {
    sidebarModule.setDrawer(val)
  }
  selectedItem = 1
  items = [
    {
      icon: 'mdi-face',
      title: 'Авторы',
      to: '/authors'
    },
    {
      icon: 'mdi-bookshelf',
      title: 'Книги',
      to: '/books'
    }
  ]
}
