import { VuexModule, Module, Mutation, getModule } from 'vuex-module-decorators'
import store from '@/store'

export interface SidebarState {
  drawer: string
}

@Module({ store, dynamic: true, name: 'sidebar' })
class SidebarModule extends VuexModule implements SidebarState {
  drawer = 'SET_DRAWER'

  @Mutation
  private setDrawer(payload: string) {
    this.drawer = payload
  }
}

export const sidebarModule = getModule(SidebarModule)
