


































































































import Vue from 'vue'
import { Component } from 'vue-property-decorator'
import { sidebarModule } from '@/store/modules/sidebar.ts'

@Component
export default class AppHeader extends Vue {
  notifications = [
    'Mike John Responded to your email',
    'You have 5 new tasks',
    "You're now friends with Andrew",
    'Another Notification',
    'Another one'
  ]

  toggleSidebar(drawer: string) {
    sidebarModule.setDrawer(drawer)
  }

  get drawer() {
    return sidebarModule.drawer
  }
}
